import React from 'react';
import { graphql, Link } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import Layout from '../../components/layout';

const Course = ({ data }) => {
  return (
    <Layout
      page='course'
      subtitle='プラン・料金'
      subtitleEn='PRICE'
      breadcrumbs={[{ link: '/course/', label: 'プラン・料金' }]}
    >
      <main className='l-main'>
        <div className='wp-wrap-post'>
          <div data-row-pc='3' data-row-sp='1'>
            {[
              {
                link: '/course/personal/',
                image: data.Image01,
                label: 'パーソナルトレーニング'
              }
            ].map(({ link, image, label }) => (
              <Link key={link} to={link} className='c-panel c-panel--col'>
                <div className='c-panel__img'>
                  <GatsbyImage
                    image={image.childImageSharp.gatsbyImageData}
                    alt={label}
                  />
                </div>
                <div className='c-panel__detail'>
                  <div className='c-panel__title'>{label}</div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    Image01: file(relativePath: { eq: "thumbnail_personal-640x350.jpg" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
  }
`;

export default Course;
